'use client'

import { PublicRoute } from '@/components/auth/auth-guard'
import AuthPage from '@/legacy-app/pages/auth'
import { Suspense } from 'react'

export default function Page() {
    return (
        <PublicRoute>
            <Suspense fallback={<div>Loading...</div>}>
                <AuthPage />
            </Suspense>
        </PublicRoute>
    )
}
